import { useState, useEffect } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { ArrowRightCircle } from 'react-bootstrap-icons'
import TrackVisibility from 'react-on-screen'
import { Link } from 'react-scroll'
import astro2 from '../assets/img/astro2.png'

export const Banner = () => {
	const [loopNum, setLoopNum] = useState(0)
	const [isDeleting, setIsDeleting] = useState(false)
	const [text, setText] = useState('')
	const [delta, setDelta] = useState(300 - Math.random() * 100)
	const [index, setIndex] = useState(1)
	const toRotate = ['Frontend Developer', 'React Developer', 'JavaScript Developer', 'UI/UX Developer']

	const period = 2000

	useEffect(() => {
		let ticker = setInterval(() => {
			tick()
		}, delta)

		return () => {
			clearInterval(ticker)
		}
	}, [text])

	const tick = () => {
		let i = loopNum % toRotate.length
		let fullText = toRotate[i]
		let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1)

		setText(updatedText)

		if (isDeleting) {
			setDelta(prevDelta => prevDelta / 2)
		}

		if (!isDeleting && updatedText === fullText) {
			setIsDeleting(true)
			setIndex(prevIndex => prevIndex - 1)
			setDelta(period)
		} else if (isDeleting && updatedText === '') {
			setIsDeleting(false)
			setLoopNum(loopNum + 1)
			setIndex(1)
			setDelta(500)
		} else {
			setIndex(prevIndex => prevIndex + 1)
		}
	}

	return (
		<section className="banner" id="home">
			<Container>
				<Row className="align-items-center">
					<Col xs={12} md={6} xl={7}>
						<span className="tagline">Welcome to my Portfolio</span>
						<h1>
							{`Hi, I'm Filip`} <span className="wrap">{text}</span>
						</h1>
						<p>
							Explore my portfolio to discover the dynamic projects that showcase my dedication to creating innovative
							and user-friendly websites."
						</p>
						<Link to="connect" duration={500}>
							<button>
								Let's connect
								<ArrowRightCircle size={25} />
							</button>
						</Link>
					</Col>
					<Col xs={12} md={6} xl={5}>
						{' '}
						<TrackVisibility once>
							{({ isVisible }) => (
								<div className={isVisible ? 'animate__animated animate__zoomIn' : ''}>
									<img src={astro2} alt="Header" style={{ borderRadius: '50%' }} />
								</div>
							)}
						</TrackVisibility>
					</Col>
				</Row>
			</Container>
		</section>
	)
}
